import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {APP_BASE_HREF, HashLocationStrategy, LocationStrategy} from '@angular/common';

import { RouterModule , Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { NavbarComponent } from './componenet/navbar/navbar.component';
import { LoginComponent } from './componenet/login/login.component';
import { RegisterComponent } from './componenet/register/register.component';
import { HomeComponent } from './componenet/home/home.component';
import { UserService } from './services/user.service';
import { TasksService } from './services/tasks.service';

import { AddTaskComponent } from './componenet/add-task/add-task.component';
import { AuthGuard } from './guards/auth.guard';
import { MainComponent } from './componenet/main/main.component';

const AppRoutes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'main', component: MainComponent, canActivate: [AuthGuard]},
  { path: 'addTask', component: AddTaskComponent, canActivate: [AuthGuard]},
  { path: 'register', component: RegisterComponent },
  { path: 'login', component: LoginComponent },
];


@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    AddTaskComponent,
    MainComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(AppRoutes),
  ],
  providers: [UserService,
    TasksService,
    AuthGuard, { provide: LocationStrategy, useClass: HashLocationStrategy },
    {provide: APP_BASE_HREF, useValue: '/'}],
    
  bootstrap: [AppComponent]
})
export class AppModule { }
